import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';





/* ################################################### */
/*          UI Framework         */
/* ################################################### */

import {
    Grid , Row , Col
    , FormGroup , FormControl , InputGroup
}                                                   from 'react-bootstrap';

import {
    RaisedButton
    , Card , CardHeader , CardActions
}                                                   from 'material-ui';

import FontAwesome                                  from 'react-fontawesome';







/* ################################################### */
/*          redux         */
/* ################################################### */
import ReduxActionGlobalMenu , { ReduxEnumGlobalMenu } 			from '../../_redux/action/reduxAction.global.menu';
import ReduxActionGlobal , { ReduxEnumGlobal } 		            from '../../_redux/action/reduxAction.global';
import ReduxActionLogin , { ReduxEnumLogin } 		            from '../../_redux/action/reduxAction.login';









/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../_abstract/c.abstract';
import ComponentCustomLoading                       from '../_component/loading/cc.loading';

import ComponentCustomAlert                         from '../_component/alert/cc.alert';







/* ################################################### */
/* 		 util		 */
/* ################################################### */

import HelperNavigate , { HelperNavigateENUM }		from '../_helper/h.navigate';
import HelperStorage								from '../_helper/h.storage';
import HelperAuthority                              from '../_helper/h.authority';

import TokenUtil                        			from '../../_util/util.token';
import { ArrayUtil }                                from '../../_util/util.array';
import { DateUtil }                                 from '../../_util/util.date';
import { ObjectUtil }                               from '../../_util/util.object';

import AmBankCrypto                                 from '../../ambank/crypto';





/* ################################################### */
/*          style         */
/* ################################################### */

import './s.login.css';




export default connect(
        ( state , ownProps ) => {

            let thisState = state[ 'ReduxLogin' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumLogin.INIT_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumLogin.LOGIN_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestSubmitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumLogin.CRYPTO_DONE == recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestCryptoDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

         class ComponentLogin extends ComponentAbstract {

            constructor( props ) {
                super( props );

                console.log( 'ComponentLogin : constructor : *** : [' + this.uuid() + '] ' , props );

                this.state = {
                    isLoading : false
                    , isSubmit : false

                    , isError : ObjectUtil.hasProperty( props.location.state , 'error' )
                    , errorMsg : ObjectUtil.hasProperty( props.location.state , 'error' ) ? props.location.state.error : ''

                    , form : {
                        loginId : ''
                        , loginPass : ''
                    }

                    , data : {

                    }
                }

            }













            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				listener
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            _onReady() {
                this.requestInit();

                this.dispatch( ReduxActionGlobalMenu.rHideMenu() );
            }













            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				event
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            onChangeField( e ) {
                let form = this.state.form;
                form[ e.target.id ] = e.target.value;
                this.setState( { form : form } );
            }
            onTogglePasswordView( ) {
                this.setState( {
                    isAbleViewPass : !this.state.isAbleViewPass
                } );
            }

            onClickSubmit() {
                this.requestCrypto();
            }










            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				request
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            requestInit() {
                console.log( 'ComponentLogin : requestInit : *** ' );


                this.setState(
                    { isLoading : true }
                    , () => {
                        this.dispatch(
                            ReduxActionLogin.rInit(
                                { }
                                , { }
                            )
                        )
                    }
                );

            }
            requestInitDone( result ) {
                console.log( 'ComponentLogin : requestInitDone : => ' , result );

                this.setState( {
                    isLoading   : false
                } );
                this.dispatch( ReduxActionLogin.rRequestEnd( ) );
            }



            requestCrypto() {
                console.log( 'ComponentLogin : requestCrypto : *** ' );

                this.setState(
                    { isLoading : true }
                    , () => {
                        this.dispatch(
                            ReduxActionLogin.rCrypto(
                                { }
                                , {
                                    count : 1
                                    , type : 'hash'
                                }
                            )
                        )
                    }
                );
            }
            requestCryptoDone( result ) {
                console.log( 'ComponentLogin : requestCryptoDone : => ' , result );

                let data = this.state.data;
                data[ 'cryptography' ] = result[ 'cryptography' ];
                this.setState( {
                    data      : data
                } , () => {
                    this.requestSubmit();
                } );
                this.dispatch( ReduxActionLogin.rRequestEnd( ) );
            }


            requestSubmit() {
                console.log( 'ComponentLogin : requestSubmit : *** ' );
                console.log( 'ComponentLogin : requestSubmit : [DATA] => ' , this.state.data );

                this.setState(
                    { isLoading : true }
                    , () => {

                        let crypto = this.state.data[ 'cryptography' ][ 0 ];

                        console.log( 'ComponentLogin : requestSubmit : *** : crypto => ' , crypto );

                        let tempPublic = ArrayUtil.split( crypto[ 'publicKey' ] , ',' );
                        let publicKey1 = tempPublic[ 0 ];
                        let publicKey2 = tempPublic[ 1 ];
                        let randomKey = crypto[ 'randomKey' ];
                        let sessionId = crypto[ 'sessionId' ];
                        let type = crypto[ 'type' ];

                        AmBankCrypto.encryptPasswd(
                            this.state.form.loginPass
                            , publicKey2
                            , publicKey1
                            , randomKey
                        ).then(
                            ( value ) => {

                                this.dispatch(
                                    ReduxActionLogin.rLogin(
                                        {}
                                        , {
                                            loginId             : this.state.form.loginId
                                            , loginPass         : value
                                            , cryptoSessionId   : sessionId
                                            , cryptoType        : type
                                        }
                                    )
                                );

                            }
                        ).catch(
                            ( error ) => {
                                console.log( 'ComponentLogin : requestLogin : [EXCEPTION - ENCRYPT] , ' , error );

                                this.setState( { isLoading : false } , () => {
                                    this.setState( {
                                        isError : true
                                        , errorMsg : 'Login Failure , please contact administrator.'
                                    } );
                                } );
                            }
                        );

                    }
                );
            }
            requestSubmitDone( result ) {
                console.log( 'ComponentLogin : requestSubmitDone : => ' , result );


                        Promise.all( [
                            ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.IS_LOGIN , 'login' )
                            , ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN , result[ 'access_token' ] )
                            , ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_REFRESH_TOKEN , result[ 'refresh_token' ] )
                            , ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE , result[ 'token_type' ] )
                            , ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_EXPIRED_IN , result[ 'expires_in' ] )
                            , ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_DATE , DateUtil.formatDateTimeStick( DateUtil.currentDate() ) )
                            , ( new HelperStorage() ).set( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_LOGIN_ID , this.state.form.loginId )
                        ] ).then( () => {

                            Promise.all( [
                                ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.IS_LOGIN )
                                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_REFRESH_TOKEN )
                                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_EXPIRED_IN )
                                , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_LOGIN_ID )
                            ] ).then(
                                ( value ) => {

                                    console.log( 'ComponentLogin : requestSubmitDone : [SAVE] => ' , value );

                                    let tokenObj = TokenUtil.decode( value[ 1 ] );
                                    console.log( 'ComponentLogin : requestSubmitDone : [SAVE - TOKEN OBJECT] => ' , tokenObj );

                                    let authority = tokenObj[ 'authorities' ];
                                    console.log( 'ComponentLogin : requestSubmitDone : [SAVE - TOKEN OBJECT] : authority => ' , authority );

                                    ( new HelperAuthority() ).load( authority )
                                                .then( () => {

                                                    this.setState(
                                                        { isLoading : false }
                                                        , () => {

                                                            this.dispatch( ReduxActionGlobal.rLoginSuccess( ) );
                                                            HelperNavigate.route(
                                                                HelperNavigate.mapPath( HelperNavigateENUM.HOME )
                                                            );

                                                        }
                                                    );
                                                } ).catch( ( e ) => {
                                                    this.setState( { isLoading : false } );
                                                    console.log( 'ComponentLogin : requestSubmitDone : [SAVE] : [EXCEPTION] , ' , e );

                                                } );

                                }
                            );

                        } )

                this.dispatch( ReduxActionLogin.rRequestEnd( ) );
            }















            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				util
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            validateField( keyId ) {
                let tmpValue = this.state[ keyId ];
                if ( tmpValue.length === 0 ) {
                    return 'error';
                }
            }










            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				render
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            render() {

                return (

                    <div>

                        <Card   className=" login-card ">

                            <CardHeader
                                        title="Login"
                                        titleStyle={ { fontSize : 24 , margin : '20px 0px 0px 50px' } } />

                            <CardActions>

                                <form>

                                    <Grid fluid>

                                        <Row className=" show-grid ">
                                            <Col xs={ 2 } md={ 2 }>
                                                <FontAwesome
                                                                name="user"
                                                                style={ { float : 'right' , marginTop : '10px' } } />
                                            </Col>

                                            <Col xs={ 9 } md={ 9 }>
                                                <FormGroup            controlId="loginId" >
                                                    <FormControl
                                                                    type="text"
                                                                    value={ this.state.form.loginId }
                                                                    placeholder="Login ID"
                                                                    onChange={ ( e ) => this.onChangeField( e ) }
                                                    />

                                                    <FormControl.Feedback />
                                                </FormGroup>
                                            </Col>
                                        </Row>

                                        <Row className=" show-grid ">
                                            <Col xs={ 2 } md={ 2 }>
                                                <FontAwesome
                                                                  name="key"
                                                                  style={ { float : 'right' , marginTop : '10px' } } />
                                            </Col>

                                            <Col xs={ 9 } md={ 9 }>
                                                <FormGroup            controlId="loginPass" >

                                                    <InputGroup>
                                                        <FormControl
                                                                            type={ this.state.isAbleViewPass ? "text" : "password" }
                                                                            value={ this.state.form.loginPass }
                                                                            placeholder="Password"
                                                                            onChange={ ( e ) => this.onChangeField( e ) }
                                                        />

                                                        <InputGroup.Addon  onClick={ () => this.onTogglePasswordView() }>
                                                            {
                                                                this.state.isAbleViewPass
                                                                ?
                                                                    ( <FontAwesome   className={ ' login-marginTop-10 login-marginLeft-20 ' }   name="eye-slash" /> )
                                                                :
                                                                    ( <FontAwesome   className={ ' login-marginTop-10 login-marginLeft-20 ' }   name="eye" /> )
                                                            }
                                                        </InputGroup.Addon>
                                                    </InputGroup>

                                                    <FormControl.Feedback />
                                                </FormGroup>
                                            </Col>
                                        </Row>

                                    </Grid>



                                    <div className=" login-card-footer ">

                                        <RaisedButton       icon={ <FontAwesome name="floppy-o" /> }
                                                           labelPosition="after"
                                                           style={ { margin : '10px' } }
                                                           label="Submit"
                                                           onClick={ () => {
                                                               this.onClickSubmit()
                                                           } }
                                       />

                                    </div>

                                </form>

                            </CardActions>

                        </Card>



                        <ComponentCustomLoading
                                    visible={ this.state.isLoading } />


                        <ComponentCustomAlert
                            visible={ this.state.isError }

                            title={ 'Session Error' }
                            msg={ this.state.errorMsg }

                            onClose={ () => {
                                this.setState( {  isError : false } );
                            } }

                            alertType={ 'error' }
                        />

                    </div>

                )
            }

        }

    )



